.auto-sel-cont {
    padding: 16px;
    font-size: 16px;
    cursor: pointer;
}


.auto-sel {
    position: relative;
    display: inline-block;
}

.auto-sel-content {
    position: absolute;
    background-color: #f1f1f1;
    min-width: 160px;
    overflow: auto;
    -webkit-box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
            box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
}

.auto-sel-content div {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
}


.sel-item{background-color: red;}




